.rightbar {
    flex: 4;
}
.rightbarWrapper {
    padding: 20px 20px 0 0;
}
.birthdayContainer {
    display: flex;
    align-items: center;
}
.birthdayImg {
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.birthText {
    font-weight: 300;
    font-size: 15px;
}

.rightbarAd {
    width: 100%;
    border-radius: 10px;
    margin: 30px 0;
}

.rightbarTittle {
    margin-bottom: 20px;
}
.rightbarOnlineFriendList {
    padding: 0;
    margin: 0;
    list-style: none;
}

.rightbarTittle {
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}
.rightbarInfo {
    margin-bottom: 30px;
}
.rightbarInfoItem {
    margin-bottom: 10px;
}
.rightbarInfoKey {
    font-weight: 500;
    margin-right: 15px;
    color: #555;
}
.rightbarInfoValue {
    font-weight: 300;
}

.rightbarFollowings {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.rightbarfollowing {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}

.rightbarfolloingImg {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
}
